<template>
  <div class="row mt-0 d-flex align-items-start hide-scrollbar" id="login-page">
    <div class="col-lg-8 h-100 flex-center">
      <img src="/image/primary_letter.svg" alt="" class="auth-logo" />
      <img
        :src="`/image/forgot-password-v2${isDark ? '-dark' : ''}.svg`"
        class="auth-img"
        alt=""
      />
    </div>
    <div class="col-lg-4 px-0 flex-center">
      <div
        class="justify-content-center login-box box flex-column"
        v-if="index == 0"
      >
        <h2 class="mb-3">Forgot Password? </h2>
        <p class="mb-4 normal-text gray">
          Enter your email and we'll send you instructions to reset your
          password
        </p>
        <form @submit.prevent="onSubmit" class="w-100">
          <label for="Email" class="custom-label">Email</label>
          <el-input
            name="Email"
            placeholder="Enter your email"
            class="w-100 mb-3"
            v-model="email"
          />
          <el-button
            :loading="isLoading"
            class="w-100 mx-0 mb-3"
            type="primary"
            native-type="primary"
            >Send reset password link</el-button
          >
        </form>
        <div class="flex-center w-100 my-2">
          <!-- <a class="primary pointer md-text">Create new account</a> -->
          <a
            class="primary hover-underline pointer md-text"
            @click="$router.go(-1)"
            >Back</a
          >
        </div>
      </div>
      <div
        class="justify-content-center login-box box flex-column"
        v-if="index == 1"
      >
        <h2 class="mb-3">Email Sent</h2>
        <p class="mb-4 normal-text gray">
          We've sent verification code to your email, please check your email
          and enter 6-pin code to continue resetting your password.
        </p>

        <form @submit.prevent="onVerify" class="w-100">
          <label for="Email" class="custom-label">Pin code</label>
          <PincodeInput :length="6" v-model="code" />
          <el-button
            :loading="isLoading"
            class="w-100 mx-0 mb-3"
            type="primary"
            native-type="primary"
            >Verify</el-button
          >
        </form>
        <div class="flex-center w-100 my-2">
          <!-- <a class="primary pointer md-text">Create new account</a> -->
          <a class="primary hover-underline pointer md-text" @click="index = 0"
            >Back</a
          >
        </div>
      </div>

      <div
        class="justify-content-center login-box box flex-column"
        v-if="index == 2"
      >
        <h2 class="mb-3">Reset Password</h2>
        <p class="mb-4 normal-text gray">Enter your new password</p>

        <form @submit.prevent="onReset" class="w-100">
          <label for="Password" class="custom-label">New Password</label>
          <el-input
            name="Password"
            class="w-100 mb-3"
            type="password"
            placeholder="Enter your new password"
            v-model="password"
          />
          <el-button
            :loading="isLoading"
            class="w-100 mx-0 mb-3"
            type="primary"
            native-type="primary"
            >Save</el-button
          >
        </form>

        <div class="flex-center w-100 my-2">
          <!-- <a class="primary pointer md-text">Create new account</a> -->
          <a class="primary hover-underline pointer md-text" @click="index = 1"
            >Back</a
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import PincodeInput from "vue-pincode-input";
export default {
  data() {
    return {
      email: "",
      password: "",
      isLoading: false,
      index: 0,
      code: null,
      uid: null,
      token: null,
    };
  },
  components: {
    PincodeInput,
  },
  computed: {
    ...mapState(["isDark"]),
  },
  created() {
    this.email = this.$route.query.email;
  },
  methods: {
    onSubmit() {
      this.isLoading = true;
      this.$axios
        .post("reset-password/", { email: this.email })
        .then((res) => {
          this.isLoading = false;
          this.$noti.display({
            color: "success",
            title: "Success",
            message: res.data.detail,
          });
          this.uid = res.data.uid;
          this.index = 1;
        })
        .catch((err) => {
          this.isLoading = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    onVerify() {
      this.isLoading = true;
      this.$axios
        .post("verify-code/", { uid: this.uid, code: this.code })
        .then((res) => {
          this.isLoading = false;
          this.$noti.display({
            color: "success",
            title: "Success",
            message: res.data.detail,
          });
          this.uid = res.data.uid;
          this.token = res.data.token;
          this.index = 2;
        })
        .catch((err) => {
          this.isLoading = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    onReset() {
      this.isLoading = true;
      this.$axios
        .put("reset-password/", {
          password: this.password,
          token: this.token,
          uid: this.uid,
        })
        .then((res) => {
          this.isLoading = false;
          this.$noti.display({
            color: "success",
            title: "Success",
            message: res.data.detail,
          });
          this.$router.push({ name: "Login" });
        })
        .catch((err) => {
          this.isLoading = false;
          this.$noti.display({ message: err, status: "error", color: "error" });
        });
    },
  },
};
</script>
<style>
.vue-pincode-input-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
input.vue-pincode-input {
  border-radius: 0.65rem;
  box-shadow: none !important;
  background: rgb(var(--input));
  color: rgb(var(--text));
  width: calc(100% / 6);
  max-width: unset !important;
  height: 38px;
}
</style>